import { mapState } from 'vuex'
import layoutDirMixin from '@/scripts/mixins/layoutDirMixin'

export default {
  name: 'NavDrawer',
  mixins: [layoutDirMixin],
  data() {
    return {
      isOpen: false
    }
  },
  computed: {
    ...mapState(['open_drawer'])
  },
  watch: {
    'open_drawer'(val) {
      this.isOpen = val
      console.log(val)
    }
  }
}
